/* Glass morphism */
.glass {
    background: hsl(var(--card) / 0.6);
    backdrop-filter: blur(20px);
    border: 1px solid hsl(var(--border) / 0.5);
  }
.glass-strong {
    background: hsl(var(--card) / 0.8);
    backdrop-filter: blur(30px);
    border: 1px solid hsl(var(--border) / 0.6);
  }
/* Glow effects */
.glow-primary {
    box-shadow: 0 0 30px hsl(var(--primary) / 0.3),
                0 0 60px hsl(var(--primary) / 0.1);
  }
/* Gradients */
.gradient-text {
    background: linear-gradient(135deg, hsl(var(--primary)), hsl(var(--accent)));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
/* Animations */
@keyframes float {
    0%, 100% { transform: translateY(0) rotate(0deg); }
    33% { transform: translateY(-12px) rotate(2deg); }
    66% { transform: translateY(-6px) rotate(-2deg); }
  }
@keyframes pulse-glow {
    0%, 100% { 
      box-shadow: 0 0 20px hsl(var(--primary) / 0.4),
                  0 0 40px hsl(var(--primary) / 0.2);
    }
    50% { 
      box-shadow: 0 0 40px hsl(var(--primary) / 0.6),
                  0 0 80px hsl(var(--primary) / 0.3);
    }
  }
@keyframes shimmer {
    0% { background-position: -200% 0; }
    100% { background-position: 200% 0; }
  }
@keyframes fade-in {
    from { opacity: 0; transform: translateY(20px); }
    to { opacity: 1; transform: translateY(0); }
  }
@keyframes scale-in {
    from { opacity: 0; transform: scale(0.9); }
    to { opacity: 1; transform: scale(1); }
  }
@keyframes slide-up {
    from { opacity: 0; transform: translateY(30px); }
    to { opacity: 1; transform: translateY(0); }
  }
@keyframes bounce-soft {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-6px); }
  }
@keyframes spin-slow {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
@keyframes gradient-shift {
    0%, 100% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
  }
@keyframes number-tick {
    0% { transform: translateY(0); opacity: 1; }
    50% { transform: translateY(-10px); opacity: 0; }
    51% { transform: translateY(10px); opacity: 0; }
    100% { transform: translateY(0); opacity: 1; }
  }
.animate-float { animation: float 6s ease-in-out infinite; }
.animate-pulse-glow { animation: pulse-glow 3s ease-in-out infinite; }
.animate-fade-in { animation: fade-in 0.6s ease-out; }
.animate-scale-in { animation: scale-in 0.4s ease-out; }
.animate-slide-up { animation: slide-up 0.5s ease-out; }
/* Hover effects */
.hover-lift {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
.hover-lift:hover {
    transform: translateY(-4px);
    box-shadow: 0 20px 40px hsl(var(--primary) / 0.15);
  }
/* Card styles */
.card-premium {
    background: linear-gradient(145deg, hsl(var(--card)), hsl(var(--card) / 0.8));
    border: 1px solid hsl(var(--border) / 0.5);
    border-radius: var(--radius);
    box-shadow: 0 4px 20px hsl(0 0% 0% / 0.3);
  }
.card-highlight {
    background: linear-gradient(145deg, hsl(var(--primary) / 0.1), hsl(var(--accent) / 0.05));
    border: 1px solid hsl(var(--primary) / 0.3);
  }
/* Button styles */
.btn-premium {
    background: linear-gradient(135deg, hsl(var(--primary)), hsl(189 94% 35%));
    color: hsl(var(--primary-foreground));
    font-weight: 600;
    padding: 0.75rem 1.5rem;
    border-radius: 0.75rem;
    transition: all 0.3s ease;
    box-shadow: 0 4px 15px hsl(var(--primary) / 0.3);
  }
.btn-premium:hover:not(:disabled) {
    transform: translateY(-2px);
    box-shadow: 0 8px 25px hsl(var(--primary) / 0.4);
  }
.btn-premium:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
.btn-secondary {
    background: hsl(var(--secondary));
    color: hsl(var(--secondary-foreground));
    font-weight: 500;
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    border: 1px solid hsl(var(--border));
    transition: all 0.2s ease;
  }
.btn-secondary:hover:not(:disabled) {
    background: hsl(var(--secondary) / 0.8);
    border-color: hsl(var(--primary) / 0.5);
  }
/* Input styles */
.input-premium {
    background: hsl(var(--muted) / 0.5);
    border: 1px solid hsl(var(--border));
    border-radius: 0.75rem;
    padding: 0.75rem 1rem;
    color: hsl(var(--foreground));
    transition: all 0.2s ease;
  }
.input-premium:focus {
    outline: none;
    border-color: hsl(var(--primary));
    box-shadow: 0 0 0 3px hsl(var(--primary) / 0.2);
  }
/* Scrollbar */
