    width: 8px;
    height: 8px;
  }
::-webkit-scrollbar-track {
    background: hsl(var(--muted));
    border-radius: 4px;
  }
::-webkit-scrollbar-thumb {
    background: hsl(var(--primary) / 0.5);
    border-radius: 4px;
  }
::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--primary) / 0.7);
